*{
  background-color: rgb(182, 180, 239);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.start{
  height: 80px;
  width: 80px;
  border-radius: 50%;
  margin: 20px;
  background-color: green;
  color: white;
}
.start:disabled{
  background-color: #6058581a;
}
.stop{
  height: 80px;
  width: 80px;
  border-radius: 50%;
  margin: 20px;
  background-color: red;
  color: white;
}
.stop:disabled{
  background-color: #61dafb;
}
.reset{
  height: 80px;
  width: 80px;
  border-radius: 50%;
  margin: 20px;
  background-color: orange;
  color: white;
}
.reset:disabled{
  background-color: antiquewhite;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
